
import dateFormat from 'vux/src/tools/date/format.js'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import ChinaAddressData from 'vux/src/datas/china_address.json'
import XAddress from 'vux/src/components/x-address/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

import {verifyPhone, verifyEmail} from '../../utils/index.js'
export default {
name: '',
components: {
Group,
XInput,
XButton,
Toast,
XAddress,
Datetime
},
data () {
return {
toastData: {
isShow: false,
type: 'warn',
text: ''
},
addressData: ChinaAddressData,
addressValue: ['黑龙江省', '哈尔滨市', '道里区'],
onLoadShow: true,
json: {
pname: '',
cname: '',
rname: '',
email: '',
mobile: '',
birthday: '',
gradeName: '',
realname: '',
sex: 1,
pic: ''
},
avatar: require('../../assets/images/my/avatar.png'),
imgData: {
accept: 'image/gif, image/jpeg, image/png, image/jpg'
}
}
},
created () {
console.log(this.addressValue)
this.getDetail()
},
methods: {
getDetail () {
this.axios.post('member/detail').then((response) => {
if (response.success) { // dateFormat(66182400000, 'YYYY-MM-DD')
let data = response.data
this.addressValue = [data.pname, data.cname, data.rname]
this.json = {
address: data.address,
pname: data.pname,
cname: data.cname,
rname: data.rname,
email: data.email,
mobile: data.mobile,
birthday: dateFormat(data.birthday, 'YYYY-MM-DD'),
gradeName: data.gradeName,
realname: data.realname,
sex: 1,
pic: data.pic
}
console.log('getrdetail', this.addressValue, this.json)
}
})
},
uploadImg (event) {
let file = event.target.files[0]

let reader = new FileReader()
let dataURL = ''
reader.onloadend = () => {

dataURL = reader.result
this.json.pic = dataURL 
console.log('dataURL', dataURL)
}
reader.readAsDataURL(file) 
},
onShadowChange (ids, names) { // 省市区改变
this.json.pname = names[0]
this.json.cname = names[1]
this.json.rname = names[2]
this.addressValue = [names[0], names[1], names[2]]
console.log('onShadowChange', this.json, names, this.addressValue)
},
hide (bool) {
if (!bool && (this.json.pname === '省')) { // 如果取消
this.onLoadShow = true
}
},
show () {
console.log('show')
},
dateChange (value) {
this.json.birthday = value
console.log('dateChange', this.json, value)
},
save () {
let _this = this
let file = this.$refs.upload.files[0]
console.log(verifyPhone, typeof this.json.mobile)
if (!this.json.mobile || !verifyPhone(this.json.mobile)) {
this.toastData = {
isShow: true,
type: 'warn',
text: '请填写正确的手机号'
}
return false
}
if (!this.json.email || !verifyEmail(this.json.email)) {
this.toastData = {
isShow: true,
type: 'warn',
text: '请填写正确的邮箱'
}
return false
}
if (file) {
let formData = new FormData()
formData.append('file', file)
console.log(formData)
this.axios.post('file/uploadImage', formData).then((response) => {
if (response.success) {
console.log('图片上传成功')
this.json.pic = response.data
} else {
this.json.pic = ''
}
}).then(() => {
this.axios.post('member/memberedit', this.json).then((response) => {
_this.toastFunc(response.success)
let timer = setTimeout(() => {
this.$router.push({name: 'my'})
clearTimeout(timer)
}, 1000)
})
})
} else {
this.axios.post('member/memberedit', this.json).then((response) => {
_this.toastFunc(response.success)
let timer = setTimeout(() => {
this.$router.push({name: 'my'})
clearTimeout(timer)
}, 1000)
})
}
},
toastFunc (bool) {
if (bool) {
this.toastData = {
isShow: true,
type: 'success',
text: '编辑成功'
}
} else {
this.toastData = {
isShow: true,
type: 'warn',
text: '保存失败'
}
}
}
}
}



